<template>
  <div style="height: 100%; width: 100%">
    <div style="height: 10%">
      <v-layout row fill-height align-center justify-start>
        <v-flex xs2>
          <span><h3>Creat Type</h3></span>
        </v-flex>
        <v-flex xs10>
          <v-layout align-start>
            <template v-for="(item, index) in btns" >
              <v-btn 
                :key="index"
                v-on:click="create(item.bind)"
              >{{item.name}}</v-btn>
            </template>
          </v-layout>
        </v-flex>
      </v-layout>
    </div>
    <l-map style="height: 90%; width: 100%" :zoom="zoom" :center="center" @click="addType">
      <l-tile-layer :url="url" :attribution="attribution"/>
      <l-marker
        v-for="( marker, index ) in markers" 
        :lat-lng="marker" 
        @click="removeMarker(index)"
        :key="index"
      />
      <l-rectangle
        :bounds="newRectangle.bounds"
        :l-style="newRectangle.style" 
      />
      <l-circle
        :lat-lng="newCircle.center"
        :radius="newCircle.radius"
        :color="newCircle.color"
      />
      <l-polyline
        :lat-lngs="newPolyline.latlngs"
        :color="newPolyline.color"
      />
      <l-polygon
        :lat-lngs="newPolygon.latlngs"
        :color="newPolygon.color"
      />
    </l-map>
  </div>
</template>

<script>

export default {
  data () {
    return {
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      zoom: 7,
      center: [36.123435, -231.514893],
      markerLatLng: [47.313220, -1.319482],
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      btns:[
        { name: 'marker',     bind:'createMarker' },
        { name: 'ractangle',  bind:'createRectangle' },
        { name: 'cercle',     bind:'createCercle' },
        { name: 'polyline',   bind:'createPolyline' },
        { name: 'polygon',    bind:'createPolygon' },
        { name: 'clear',      bind:'remove' }
      ],
      type:'',
      markers: [
        // `L.latLng(47.412, -1.218),
        // L.latLng(47.413220, -1.219482),
        // L.latLng(47.414, -1.22)`
      ],
      newRectangle: {
        bounds: [[4.341456, -1.397133], [47.303901, -1.243813]],
        style: { color: 'green', weight: 5 }
      },
      newCircle: {
        center: [47.413220, -1.0482],
        radius: 6000,
        color: 'yellow'
      },
      newPolyline:{
        latlngs:[],
        color: 'blue'
      },
      newPolygon:{
        latlngs:[],
        color: 'red'
      }
    };
  },
  mounted(){
    this.type='addMarker';
  },
  methods: {
    create(item){
      this[item]();
    },
    addType(event){
      this[this.type](event);
    },
    createMarker(){
      this.type='addMarker';
    },
    createRectangle(){
      this.type='addRectangle';
    },
    createCercle(){
      this.type='addCercle';
    },
    createPolyline(){
      this.type='addPolyline';
    },
    createPolygon(){
      this.type='addPolygon';
    },
    removeMarker(index) {
      this.markers.splice(index, 1);
    },
    addMarker(event) {
      this.markers.push(event.latlng);
    },
    addRectangle(event){
      var lat = event.latlng.lat;
      var lng = event.latlng.lng;
      var point = [lat, lng];
      var point1 = [lat+2, lng+2];
      var points = [point, point1];

      this.newRectangle.bounds = points;
      console.log("this.newRectangle",this.newRectangle);
    },
    addCercle(event){
      var lat = event.latlng.lat;
      var lng = event.latlng.lng;
      var point = [lat, lng];

      this.newCircle.center = point;
    },
    addPolyline(event) {
      var lat = event.latlng.lat;
      var lng = event.latlng.lng;
      var point = [lat, lng];
      
      this.newPolyline.latlngs.push(point);
    },
    addPolygon(event) {
      var lat = event.latlng.lat;
      var lng = event.latlng.lng;
      var point = [lat, lng];
      
      this.newPolygon.latlngs.push(point);
    },
    remove(){
      this.markers = [
        // `L.latLng(47.412, -1.218),
        // L.latLng(47.413220, -1.219482),
        // L.latLng(47.414, -1.22)`
      ];
      this.newRectangle = {
        bounds: [[4.341456, -1.397133], [47.303901, -1.243813]],
        style: { color: 'green', weight: 5 }
      },
      this.newCircle = {
        center: [47.413220, -1.0482],
        radius: 6000,
        color: 'yellow'
      },
      this.newPolyline = {
        latlngs:[],
        color: 'blue'
      };
      this.newPolygon = {
        latlngs:[],
        color: 'red'
      };
    }
  }
}
</script>